body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #1a1a1a 0%, #0a0a0a 100%);
  min-height: 100vh;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@1,500&display=swap');

body {
  font-family: 'Josefin Sans', sans-serif; 
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.app-title {
  font-size: 2.5rem;
  font-weight: 600;
  text-align: center;
  margin: 40px 0;
  background: linear-gradient(90deg, #00C805 0%, #00A3FF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.5px;
}

/* Style the Material-UI components */
.MuiTextField-root {
  background: rgba(255, 255, 255, 0.05) !important;
  border-radius: 8px !important;
  margin-right: 16px !important;
}

.MuiInputBase-input {
  color: white !important;
}

.MuiButton-contained {
  background: linear-gradient(90deg, #00C805 0%, #00A3FF 100%) !important;
  border-radius: 8px !important;
  text-transform: none !important;
  font-weight: 600 !important;
  padding: 10px 24px !important;
  box-shadow: none !important;
}

.MuiButton-contained:hover {
  opacity: 0.9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2) !important;
}

@media (max-width: 950px) {
  .app-title {
    font-size: 2rem;
    margin-top: 30px;
  }
  
  .App {
    padding: 10px;
  }
}

/* Add smooth animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.stock-card-enter {
  animation: fadeIn 0.3s ease-out forwards;
}